.artikel-section {
    padding: 100px 15vw;
}

.cerita-load {
    min-height: 50vh;
}
.vote {
    cursor: pointer;
    width: 15px;
    height: 15px;
}

.title-cerita {
    font-family: 'Bebas Neue';
    font-size: 60px;
}

.body-cerita {
    white-space: pre-wrap;
}

.tanggal-cerita {
    color: #EC6F00;
}

.like-button {
    border-radius: 500px;
    border: 2px solid black;
}

.btn-like {
    position: relative;
}

.button--likeload::after {
    content: "";
    position: absolute;
    width: 16px;
    height: 16px;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    border: 4px solid transparent;
    border-top-color: black;
    border-radius: 50%;
    animation: button-likeload-spinner 1s ease infinite;
}

.trigger-hidden {
    cursor: pointer;
}

.btn-comment {
    position: relative;
}

@keyframes button-likeload-spinner {
    from {
        transform: rotate(0turn);
    }

    to {
        transform: rotate(1turn);
    }
}

.jumlah-like {
    background-color: #E3E3E3;
    border-radius: 39px;
}

.custom-komentar {
    background-color: #F4F4F4;
    padding: 0 15vw;
    width: 100%;
}

.komentar-box {
    border-bottom: 1px solid #737373;
}

.btn-black {
    background-color: black;
    border-radius: 0;
    color: white;
}

.btn-black:hover {
    color: white;
}

.komentar {
    border-bottom: 1px solid #737373;
}

.cerita-load {
    min-height: 60vh;
}

@media(max-width: 992px) {
    .title-cerita {
        font-size: 40px;
    }
}

@media(max-width: 576px) {
    .title-cerita {
        font-size: 20px;
    }

    .artikel-section {
        padding: 50px 5vw;
    }

    .custom-komentar {
        padding: 0 5vw;
    }

    .btn-share {
        width: 20px;
        height: 20px;
    }

    .text-author {
        font-size: 12px;
    }

    .text-date {
        font-size: 10px;
    }

    .body-cerita {
        font-size: 12px;
    }
}